import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute, Params, ParamMap } from '@angular/router';
import { Message } from 'primeng/primeng';
// import { IsPrefFlg, IsNegotiable, IsContact, IsDifferWay } from './../../constant/codeValue';
import { CarryOutBean } from './../bean/carryout.bean';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-carry-out-detail',
  templateUrl: './carry-out-detail.component.html',
  styleUrls: ['./carry-out-detail.component.scss'],
  providers: [Commfunc]
})
export class CarryOutDetailComponent implements OnInit {
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //传入详情的值
  carryOutBean: CarryOutBean = new CarryOutBean();
  IsPrefFlg: any = [];
  isNegotiable: any = [];
  isContact: any = [];
  isDifferWay: any = [];
  isUnit: any = [];
  plnRsetCode: string;
  plnRsetNum: string;
  rsetFlag: string;
  reprncPlnNum: string;
  reprncFlg: string;
  prefVal: string;
  prefWayCode: string;
  prefPlnNum: string;
  prefFlg: string;
  intRateVal: string;
  intRateCode: string;
  chinName: string;
  intRateCgy: string;
  txnDt: string;
  txInf: string;
  cusmNum: string;
  pdNum: string;
  txnSeqNum: string;
  code: any;//获取所有码值
  constructor(
    public httpService: ValuationHttpService,
    public route: ActivatedRoute,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.IsPrefFlg = this.code['IsPrefFlg'];
    this.isNegotiable = this.code['IsNegotiable'];
    this.isContact = this.code['IsContact'];
    this.isDifferWay = this.code['IsDifferWay'];
    this.isUnit = this.code['IsUnit'];
  }

  ngOnInit() {
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //确定
  confirm() {
    this.outValue.emit(false);
  }
}
