import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmationService, Message } from 'primeng/primeng';
import { CarryOutBean } from './bean/carryout.bean';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
// import { IsPrefFlg, IsNegotiable, IsXieIcon1, IsDifferWay1, IsDifferWay2 } from './../constant/codeValue';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-carry-out',
  templateUrl: './carry-out.component.html',
  styleUrls: ['./carry-out.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class CarryOut implements OnInit {
  carryOutBean: CarryOutBean = new CarryOutBean();
  date3;
  invalDt;
  cusmNum;
  intRateName;
  rsetFlag;
  ch: any = CALENDAR_CN;
  //table数据
  list: any = [];
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  //提示信息
  msgs: Message[] = [];
  total: number = 10;
  first: number = 0;//分页控制
  tabMesg: any;//表格中无数据时显示的内容
  IsPrefFlg: any = [];
  isNegotiable: any = [];
  IsXieIcon: any = [];
  isDifferWay: any = [];
  isDifferWay1: any = [];
  isUnit: any = [];
  code: any;//获取所有码值
  constructor(
    public valuationHttpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc) {
    this.codeValues();
    this.IsPrefFlg = this.code['IsPrefFlg'];
    this.isNegotiable = this.code['IsNegotiable'];
    this.IsXieIcon = this.code['IsXieIcon'];
    this.isDifferWay = this.code['IsDifferWay'];
    this.isDifferWay1 = this.code['IsDifferWay1'];
    this.isUnit = this.code['IsUnit'];
  }

  ngOnInit() {
    this.carryOutBean.pageSize = 10;
    this.carryOutBean.pageNum = 1;
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  querySecle() {
    this.carryOutBean.pageSize = 10;
    this.carryOutBean.pageNum = 1;
    this.first = 0;
    this.toquery();
  }
  toquery() {
    this.valuationHttpService.findRateTrialService(this.carryOutBean).subscribe(data => {
      if (data.resultList == "" || data.resultList == null) {
        this.tabMesg = tableMessage
      }
      this.list = data.resultList
      this.total = data.total;
      // 错误条件查询的提示
      if (data.returnCode.code != "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, err => {
      this.tabMesg = tableMessage
    })
  }
  //详情
  detail(param) {
    this.headerTitle = '利率计算详情';
    this.showModel = '1';
    this.display = true;
    this.inValueCode = param;
  }
  //详情的回调方法
  detailCall(param) {
    this.display = param;
  }
  //重置
  newSet() {
    this.carryOutBean = new CarryOutBean();
    this.carryOutBean.pageSize = 10;
    this.carryOutBean.pageNum = 1;
    // this.beanValue.dmsnlCode='';
    // this.beanValue.dmsnlChiNm='';
    // this.first =0;
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.carryOutBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.carryOutBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.carryOutBean.pageSize;
    //调用查询的方法
    this.toquery();
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}